// _index.scss
body {
    margin: 0;
    height: 100vh;
    background-position: center;
    background-color: $background-color;
    background-image: url(../assets/images/background2.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    backdrop-filter: blur(5px);
    
    .video-background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: -1;
    }

    .content {
        position: relative;
        z-index: 1;
        text-align: center;
        color: white;
        padding: 20px;

        .header {
            h1 {
                position: relative;
                margin-top: 55px;
                font-size: 85px;
                font-style: italic;
                color: rgb(232, 232, 240);
            }

            h2 {
                margin: 0;
                font-size: 75px;
                font-style: italic;
                color: rgb(232, 232, 240);
            }

            h4 {
                color: rgb(247, 245, 242);
                margin: 0;
                height: 30px;
                font-size: 30px;
                transform: translateY(-100%);
                opacity: 0;
                animation: slideInFromTop 1.5s ease-out forwards;
            }
        }

        button {
            padding: 10px 20px;
            font-size: 20px;
            color: rgb(244, 234, 234);
            background-color: $button-bg;
            border: none;
            border-radius: 20px;
            cursor: pointer;
            transition: background-color 0.3s;
            position: absolute;
            right: 20px;
            bottom: 50px;

            &:hover {
                background-color: $button-hover-bg;
            }
        }
    }
}
