// _main.scss
.header1 {
    h1 {
        width: 100%;
        text-align: center;
        font-size: 60px;
        color: $header-font-color;
        top: 25px;
        font-style: italic;
    }
}

.container {
    position: relative;
    width: 500px;
    height: auto;
    margin: 20px auto;
}

.inputField {
    width: 100%;
    padding: 20px;
    border: 2px solid #000;
    background-color: #d2d4dcd8;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: left;
    opacity: 0;
    transform: translateX(-100%);
    animation: slideInLeft 2.5s ease-out forwards;

    h1 {
        text-align: center;
    }

    button {
        width: 150px;
        height: 40px;
        background-color: $button-bg;
        color: rgb(244, 234, 234);
        border: none;
        border-radius: 30px;
        cursor: pointer;
        margin-top: 20px;
        margin-left: calc(50% - 75px);

        &:hover {
            background-color: $button-hover-bg;
        }
    }
}
